  background: #d4d4d4;
  border-radius: 5px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #00670c;
  border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #00a308;
}
